export default {
  name: "DisclaimerDialog",
  data() {
    return {
      dialog: false,
      agreed: false
    };
  },
  watch: {
    agreed(value) {
      if (this.agreed) {
        this.dialog = false;
        localStorage.setItem("kussy-disclaimer-agreed", value);
      }
    }
  },
  beforeMount() {
    this.agreed = localStorage.getItem("kussy-disclaimer-agreed") == "true";
    console.log("", {
      a: this.agreed
    });
    if (!this.agreed) {
      setTimeout(() => this.dialog = true, 1500);
    }
  }
};