import DisclaimerDialog from "@/components/DisclaimerDialog.vue";
import { mapGetters } from "vuex";
export default {
  name: "DefaultLayout",
  components: {
    DisclaimerDialog
  },
  data() {
    return {
      menuHidden: true
    };
  },
  computed: {
    ...mapGetters("ui/sidemenu", ["items"]),
    languages() {
      return this.$i18n.availableLocales;
    },
    locale: {
      get() {
        return this.$i18n.locale;
      },
      set(val) {
        this.$i18n.locale = val;
      }
    }
  }
};